<template>
  <main class="translucent-theme background">
    <appMenu selected-element="session"></appMenu>
    <div v-if="!Object.keys(session).length" class="grid-x align-center-middle content">
      <div class="card cell shrink text-center greeting-card">
        <h1>{{getGreeting()}}<br>{{getGreetingEmojis()}}</h1>
        <div><router-link to="/EditSession" class="button primary">Открыть смену</router-link></div>
      </div>
    </div>
    <div v-if="Object.keys(session).length" class="session-info grid-x align-middle">
      <div class="session-info-block cell medium-auto small-6">
        <span class="session-info-big-number">{{time}}</span>
        <label class="session-info-big-label">{{date}}</label>
      </div>
      <div class="session-info-block cell medium-auto small-6">
        <span class="session-info-big-number">{{servicesCount}}</span>
        <label class="session-info-big-label">Услуг оказано</label>
      </div>
      <div class="session-info-block cell medium-auto small-6">
        <span class="session-info-big-number">{{goodsCount}}</span>
        <label class="session-info-big-label">Товаров продано</label>
      </div>
      <div class="session-info-block cell medium-auto small-6">
        <span class="session-info-big-number">{{revenue}}</span>
        <label class="session-info-big-label">Выручка, ₽</label>
      </div>
      <div class="session-info-block cell medium-auto small-6">
        <span class="session-info-big-number">{{costs}}</span>
        <label class="session-info-big-label">Расход, ₽</label>
      </div>
      <div class="session-info-block cell medium-auto small-6">
        <span class="session-info-big-number">{{bonus}}</span>
        <label class="session-info-big-label">Премия, ₽</label>
      </div>
      <div class="session-info-block cell medium-2 small-12">
        <router-link v-bind:to="'/EditSession/' + session.id.toString()" class="button secondary small">Изменить/закрыть смену</router-link>
      </div>
    </div>
    <div v-if="Object.keys(session).length" class="session-content">
      <div class="grid-x grid-margin-x grid-margin-y">
        <employeeCard
          v-for="employee in session.employees"
          v-bind:key="employee.id"
          v-bind:employee="employee"
          v-if="employee.role === 'master'"
          />
        <employeeCard
          v-for="employee in session.employees"
          v-bind:key="employee.id"
          v-bind:employee="employee"
          v-if="employee.role === 'officeAdmin'"
          />
        <div class="cell large-6 columns card employee-card">
          <div class="employee-card-footer grid-x grid-padding-x grid-padding-y">
            <h4 class="cell small-12">Внести расход</h4>
            <div class="cell medium-6">
              <select v-model="selectedSpendType">
                <option v-for="spend in spendTypes" v-bind:key="spend.id" v-bind:value="spend.id" v-bind:selected="spend.id === selectedSpendType">
                  {{spend.name}}
                </option>
              </select>
            </div>
            <div class="cell auto">
              <input v-model="spendSum" type="number" placeholder="Сумма"/>
            </div>
            <div class="cell shrink">
              <button class="button secondary">Сохранить</button>
            </div>
          </div>
        </div>
        <div class="cell small-12 columns card">
          <table class="unstriped operations-table">
            <thead>
              <tr>
                <th>Время</th>
                <th>Сотрудник</th>
                <th>Операция</th>
                <th>Сумма</th>
                <th>Премия</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="operation in session.operations" v-bind:key="operation.id">
                <td>{{$store.getters.getTimeFromOperation(operation)}}</td>
                <td>{{$store.getters.getEmployeeNameFromOperation(operation)}}</td>
                <td v-html="$store.getters.getOperationContent(operation)"></td>
                <td>{{$store.getters.getOperationSum(operation)}}</td>
                <td>{{$store.getters.getOperationBonus(operation)}}</td>
                <td><router-link v-bind:to="$store.getters.getOperationLink(operation)" type="button" class="button secondary small">Редактировать/удалить</router-link></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </main>
</template>

<script>
import Menu from '@/components/Menu'
import EmployeeCard from '@/components/EmployeeCard'

export default {
  name: 'Session',
  components: {
    appMenu: Menu,
    employeeCard: EmployeeCard
  },
  mounted: function () {
    document.title = this.$route.meta.title
    setInterval(() => {
      this.time = this.getTime()
      this.date = this.getDate()
    }, 1000)
  },
  data () {
    return {
      session1: {},
      session: {
        'id': 1,
        'dateOpened': '21.09.2019 09:30',
        'dateClosed': null,
        'employees': [
          {
            'id': 1,
            'name': 'Алексей Луцай',
            'role': 'officeAdmin',
            'pictureUrl': '',
            'workHours': 6
          },
          {
            'id': 2,
            'name': 'Мария Попова',
            'role': 'master',
            'pictureUrl': 'static/user_photos/мария.jpg',
            'workHours': 6
          },
          {
            'id': 3,
            'name': 'Макс Корж',
            'role': 'master',
            'pictureUrl': 'static/user_photos/макс.jpg',
            'workHours': 6
          },
          {
            'id': 4,
            'name': 'Прокофий Иванов',
            'role': 'master',
            'pictureUrl': '',
            'workHours': 6
          }
        ],
        'officeId': 1,
        'state': 'open',
        'operations': [
          {
            'operationType': 'service',
            'sessionId': 1,
            'id': 1,
            'officeId': 1,
            'type': 1,
            'startDatetime': '21.09.2019 09:30',
            'finishDatetime': '21.09.2019 10:30',
            'adminId': 1,
            'masterId': 3,
            'clientId': 1,
            'cashSum': 600,
            'cashlessSum': 0,
            'discountSum': 0,
            'adminBonus': 30,
            'masterBonus': 120,
            'score': null,
            'review': '',
            'photoUrls': [],
            'comment': ''
          },
          {
            'operationType': 'goodSell',
            'id': 2,
            'officeId': 1,
            'sessionId': 1,
            'type': 1,
            'datetime': '21.09.2019 09:30',
            'adminId': 1,
            'masterId': 3,
            'clientId': 1,
            'cashSum': 0,
            'cashlessSum': 2350,
            'discountSum': 0,
            'amount': 1,
            'adminBonus': 30,
            'masterBonus': 120,
            'comment': ''
          },
          {
            'operationType': 'goodSell',
            'id': 3,
            'officeId': 1,
            'sessionId': 1,
            'type': 2,
            'datetime': '21.09.2019 09:30',
            'adminId': 1,
            'masterId': 3,
            'clientId': 1,
            'cashSum': 0,
            'cashlessSum': 2350,
            'discountSum': 0,
            'amount': 1,
            'adminBonus': 30,
            'masterBonus': 120,
            'comment': ''
          },
          {
            'operationType': 'goodSell',
            'id': 4,
            'officeId': 1,
            'sessionId': 1,
            'type': 3,
            'datetime': '21.09.2019 09:30',
            'adminId': 1,
            'masterId': 3,
            'clientId': 1,
            'cashSum': 0,
            'cashlessSum': 2350,
            'discountSum': 0,
            'amount': 1,
            'adminBonus': 30,
            'masterBonus': 120,
            'comment': ''
          },
          {
            'operationType': 'goodSell',
            'id': 5,
            'officeId': 1,
            'sessionId': 1,
            'type': 4,
            'datetime': '21.09.2019 09:30',
            'adminId': 1,
            'masterId': 3,
            'clientId': 1,
            'cashSum': 0,
            'cashlessSum': 2350,
            'discountSum': 0,
            'amount': 1,
            'adminBonus': 30,
            'masterBonus': 120,
            'comment': ''
          },
          {
            'operationType': 'goodSell',
            'id': 6,
            'officeId': 1,
            'sessionId': 1,
            'type': 5,
            'datetime': '21.09.2019 09:30',
            'adminId': 1,
            'masterId': 3,
            'clientId': 1,
            'cashSum': 0,
            'cashlessSum': 2350,
            'discountSum': 0,
            'amount': 1,
            'adminBonus': 30,
            'masterBonus': 120,
            'comment': ''
          },
          {
            'operationType': 'spend',
            'id': 2,
            'officeId': 1,
            'sessionId': 1,
            'type': 2,
            'datetime': '21.09.2019 12:44',
            'sum': 600,
            'comment': ''
          },
          {
            'operationType': 'employeePayment',
            'id': 2,
            'officeId': 1,
            'sessionId': 1,
            'employeeId': 4,
            'type': 2,
            'datetime': '21.09.2019 18:22',
            'sum': 600,
            'comment': ''
          }
        ]
      },
      selectedSpendType: 1,
      spendSum: '',
      time: this.getTime(),
      date: this.getDate()
    }
  },
  methods: {
    getGreeting: function () {
      var date = new Date()
      var hour = date.getHours()
      if (hour <= 12) {
        return 'Доброе утро!'
      }
      if (hour > 12 && hour < 17) {
        return 'Добрый день!'
      }
      if (hour >= 17) {
        return 'Добрый вечер!'
      }
    },
    getGreetingEmojis: function () {
      var emojis = ['✂️', '😀', '💪', '👋', '😎', '👏', '🦄', '🌄', '🌅', '❤️', '🍒', '🌈', '😄', '✌️', '😉', '🙂', '✂️']
      var i = 0
      var res = ''
      while (i < 3) {
        var rnd = Math.ceil(Math.random() * emojis.length - 1)
        if (rnd < 0) {
          rnd = 0
        }
        if (rnd > emojis.length) {
          rnd = emojis.length - 1
        }
        res += emojis[rnd]
        i++
      }
      return res
    },
    getTime: function () {
      var date = new Date()
      var hours = date.getHours()
      var minutes = date.getMinutes()
      return this.$store.getters.getZeroPaddedNumber(hours, 2) + ' : ' + this.$store.getters.getZeroPaddedNumber(minutes, 2)
    },
    getDate: function () {
      var date = new Date()
      return date.toLocaleDateString('ru')
    }
  },
  computed: {
    spendTypes: {
      get () {
        return this.$store.state.spendTypes
      }
    },
    servicesCount: function () {
      if (Object.keys(this.session).length) {
        return this.session.operations.filter(operation => operation.operationType === 'service').length
      }
      return 0
    },
    goodsCount: function () {
      if (Object.keys(this.session).length) {
        return this.session.operations.filter(operation => operation.operationType === 'goodSell').length
      }
      return 0
    },
    revenue: function () {
      if (Object.keys(this.session).length) {
        var revenue = 0
        var services = this.session.operations.filter(operation => operation.operationType === 'service')
        var goods = this.session.operations.filter(operation => operation.operationType === 'goodSell')
        services.map(s => {
          revenue += s.cashSum + s.cashlessSum - s.discountSum
        })
        goods.map(g => {
          revenue += g.cashSum + g.cashlessSum - g.discountSum
        })
        return revenue
      }
      return 0
    },
    costs: function () {
      if (Object.keys(this.session).length) {
        var costs = 0
        var spends = this.session.operations.filter(operation => operation.operationType === 'spend')
        var employeePayments = this.session.operations.filter(operation => operation.operationType === 'employeePayment')
        spends.map(s => {
          costs += s.sum
        })
        employeePayments.map(e => {
          costs += e.sum
        })
        return costs
      }
      return 0
    },
    bonus: function () {
      if (Object.keys(this.session).length) {
        var bonus = 0
        var services = this.session.operations.filter(operation => operation.operationType === 'service')
        var goods = this.session.operations.filter(operation => operation.operationType === 'goodSell')
        services.map(s => {
          bonus += s.adminBonus + s.masterBonus
        })
        goods.map(g => {
          bonus += g.adminBonus + g.masterBonus
        })
        return bonus
      }
      return 0
    }
  }
}
</script>

<style>
</style>



